import ReactDOM from "react-dom"
import PlainDraggable from "plain-draggable"
import LeaderLine from "leader-line-new"
import { useEffect, useRef } from "react"
import styled from "styled-components"
import "./Game.css"
import { shuffle } from "../../util/misc"
import useWindowSize from "../../hooks/useWindowSize"

const SIZES = {
  tile: {
    height: 100,
    width: 100,
  },
}

const DraggableImageBase = styled.img`
  height: ${SIZES.tile.height}px;
  width: ${SIZES.tile.width}px;
  border: 1px solid slategray;
  border-radius: 5px;
`

const DraggableImageElement = styled(DraggableImageBase)`
  z-index: 99;
  position: absolute;
  opacity: 95%;
  border: 1px solid black;
`

const DraggableImagePlaceholder = styled(DraggableImageBase)``

const between = (x, min, max) => x >= min && x <= max
const pointInBox = (point, box) =>
  between(point.left, box.left, box.right) &&
  between(point.top, box.top, box.bottom)

const findTarget = (targets, dropLocation) =>
  targets
    .map((x) => {
      const rect = x.getBoundingClientRect()
      return {
        id: x.id,
        top: rect.y,
        left: rect.x,
        bottom: rect.y + rect.height,
        right: rect.x + rect.width,
      }
    })
    .filter((x) => pointInBox(dropLocation, x))
    .map((x) => x.id)
    .slice(0, 1)
    .shift()

const Connection = (fromId, toId, options = {}) =>
  new LeaderLine(
    document.getElementById(fromId),
    document.getElementById(toId),
    options
  )

const DraggableImageContainer = ({ children }) => {
  return ReactDOM.createPortal(children, document.getElementById("root"))
}

const moveDraggable = (draggable) => {
  console.log("move it", draggable)
  const correctPosition = document
    .getElementById(draggable.target || draggable.source)
    .getBoundingClientRect()
  draggable.left = correctPosition.x
  draggable.top = correctPosition.y
}

const DraggableImage = ({ index, image, targetIds, correctTarget }) => {
  const windowSize = useWindowSize()
  const imagePlaceholderId = `image${index}`
  const draggableDomRef = useRef()
  const draggableRef = useRef()
  useEffect(() => {
    if (!draggableDomRef || !draggableDomRef.current) return
    const targets = targetIds.map((x) => document.getElementById(x))
    const d = new PlainDraggable(draggableDomRef.current)
    d.source = imagePlaceholderId
    draggableRef.current = d
    let snapped = false
    console.log(targets)
    d.snap = {
      targets,
      edge: "inside",
    }
    d.onDrag = (moveTo) => {
      snapped = moveTo.snapped
    }
    d.onDragEnd = (moveTo) => {
      console.log(moveTo, snapped)
      if (snapped) {
        if (d.line) d.line.remove()
        d.target = findTarget(targets, moveTo)
        if (imagePlaceholderId && d.target) {
          d.line = Connection(imagePlaceholderId, d.target, {
            color: correctTarget === d.target ? "green" : "red",
          })
        }
      } else {
        reset()
      }
      moveDraggable(d)
    }

    const reset = () => {
      if (d) {
        if (d.line) {
          d.line.remove()
          d.line = null
        }
        d.target = null
      }
    }
    const cleanup = () => {
      if (d) {
        reset()
        d.remove()
      }
    }

    moveDraggable(d)
    return cleanup // Remove line on unmount
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])
  useEffect(() => moveDraggable(draggableRef.current), [windowSize])
  return (
    <>
      <DraggableImagePlaceholder
        id={imagePlaceholderId}
        src={`match-img/${image}`}
        draggable={false}
        alt={image}
      />
      <DraggableImageContainer>
        <DraggableImageElement
          src={`match-img/${image}`}
          alt={image}
          ref={draggableDomRef}
        />
      </DraggableImageContainer>
    </>
  )
}

const DraggableContainer = styled.div`
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
`

const DraggableTargetDrop = styled(DraggableImageBase)`
  background-color: #ddd;
`

const DraggableTarget = ({ id, image, index }) => {
  return (
    <div style={{ display: "flex", flexDirection: "row" }}>
      <DraggableTargetDrop id={id} />
      <DraggableImageBase
        src={`match-img/${image}`}
        alt={image}
        style={{ marginLeft: 10 }}
      />
    </div>
  )
}

const SideContainer = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  background-color: slategray;
  padding: 25px;
  border-radius: 10px;
  margin: 20px 0;
`

const toKey = (x, index) => `${x}_${index}`

const Title = styled.div`
  margin-top: 50px;
  font-size: 3rem;
  text-shadow: 2px 2px 2px slategray;
`

const MatchLayout = ({ allImages, gameImages, targetImages }) => {
  const findTargetId = (image) =>
    Object.keys(targetImages).find(
      (x) => targetImages[x] === allImages[image].match
    )

  const targetIds = Object.keys(targetImages)
  return (
    <DraggableContainer>
      <SideContainer>
        {gameImages.map((x, i) => (
          <DraggableImage
            key={toKey(x, i)}
            index={i}
            image={x}
            targetIds={targetIds}
            correctTarget={findTargetId(x)}
          />
        ))}
      </SideContainer>
      <Title>Match Game</Title>
      <SideContainer>
        {targetIds.map((x, i) => (
          <DraggableTarget
            key={toKey(x, i)}
            id={x}
            index={i}
            image={targetImages[x]}
          />
        ))}
      </SideContainer>
    </DraggableContainer>
  )
}

const Match = () => {
  const allImages = {
    "2_2.png": {
      match: "bird_image_1.png",
    },
    "3_2.png": {
      match: "cat_image_18.png",
    },
    "4_2.png": {
      match: "deer_image_13.png",
    },
    "5_2.png": {
      match: "dog_image_16.png",
    },
    "7_2.png": {
      match: "horse_image_16.png",
    },
    "8_2.png": {
      match: "boat_image_10.png",
    },
  }
  const matchCount = 5
  const gameImages = shuffle(Object.keys(allImages)).slice(0, matchCount)
  const targetImages = shuffle(
    gameImages.map((x) => allImages[x].match)
  ).reduce((all, x, i) => ({ ...all, [`target${i}`]: x }), {})
  return (
    <MatchLayout
      allImages={allImages}
      gameImages={gameImages}
      targetImages={targetImages}
    />
  )
}

export default Match
