const PracticeResultTitle = styled.b`
  color: ${(props) => props.color};
  &:before {
    content: "${(props) => (props.current ? "> " : "")}";
  }
`

const PracticeResult = ({
  question: { path, correct, agree, seenbefore },
  questionNumber,
  isCurrentImage,
}) => {
  const answer =
    !isDefined(agree) || !isDefined(seenbefore)
      ? "waiting"
      : (seenbefore && correct) || (!seenbefore && !correct)
      ? "correct"
      : "incorrect"
  const displaySettings = {
    waiting: {
      text: "Waiting on your guesses...",
      color: "black",
    },
    correct: {
      text: `You know ${AI_NAME} pretty well!`,
      color: COLORS.yes,
    },
    incorrect: {
      text: `Sorry, you don't know ${AI_NAME} very well...`,
      color: COLORS.no,
    },
  }
  const display = displaySettings[answer]
  return (
    <PracticeResultContainer current={isCurrentImage}>
      <PracticeResultTitle
        color={display.color}
        current={isCurrentImage}
        title={path}
      >
        Image {questionNumber}
      </PracticeResultTitle>
      <div>{display.text}</div>
    </PracticeResultContainer>
  )
}

