const ImageSliderOverrideStyles = ({ images, currentIndex }) => {
  const [gameSettings] = useContext(GameContext)
  const bulletContainerChildIndices = [3, 4]
  const style = (containerIndex, index, colors) => {
    const border =
      currentIndex === index ? "; border: 2px solid blue !important" : ""
    return `#practice-container > div:nth-child(1) > div:nth-child(2) > div > div:nth-child(${containerIndex}) > button:nth-child(${
      index + 1
    }) { background: linear-gradient(90deg, ${colors[0]} 50%, ${
      colors[1]
    } 50%) !important; ${border} }
      `
  }
  const bulletStyle = (index, colors) =>
    bulletContainerChildIndices.map((i) => style(i, index, colors)).join("\n")
  const bulletStyles = () =>
    images.map((_, i) => {
      const agree = answer("agree", gameSettings, images[i].path)
      const seenbefore = answer("seenbefore", gameSettings, images[i].path)
      const toColor = (x) =>
        x == null ? "inherit" : x ? COLORS.yes : COLORS.no
      return bulletStyle(i, [toColor(agree), toColor(seenbefore)])
    })

  const imageSize = {
    width:
      PRACTICE_IMAGE_SIZE.image.width / PRACTICE_IMAGE_SIZE.container.width,
    height:
      PRACTICE_IMAGE_SIZE.image.height / PRACTICE_IMAGE_SIZE.container.height,
  }
  const imagePercent = {
    width: parseInt(imageSize.width * 100),
    height: parseInt(imageSize.height * 100),
  }
  const paddingLeft =
    (PRACTICE_IMAGE_SIZE.container.width - PRACTICE_IMAGE_SIZE.image.width) / 2
  const paddingTop =
    (PRACTICE_IMAGE_SIZE.container.height - PRACTICE_IMAGE_SIZE.image.height) /
    2

  const imageStyles = () => [
    `#practice-container > div:nth-child(1) > div:nth-child(2) > div > div:nth-child(1) > div {left: ${paddingLeft}px !important; top: ${paddingTop}px !important; width: ${imagePercent.width}% !important; height: ${imagePercent.height}% !important; border-radius: 5px; box-shadow: 0 0 5px #000000;}`,
  ]
  return (
    <style
      dangerouslySetInnerHTML={{
        __html: [...bulletStyles(), ...imageStyles()].join("\n"),
      }}
    />
  )
}

