const Tunings = ({ image, getImageDiv, onLoadImageVariant }) => {
  const defaultStyle = {
    noise: 0,
  }
  const [styles, setStyles] = useState(defaultStyle)
  const [selectedVariant, setSelectedVariant] = useState(null)
  const changeImage = (key) =>
    styleDiv(getImageDiv, {
      transition: "none",
      "background-image":
        !styles[key] && key in image.variants && "path" in image.variants[key]
          ? `url("img/${image.variants[key].path}")`
          : `url("img/${image.path}")`,
    })

  const variantLabel = (key) => {
    return (image.variants[key] || image).label
  }
  const resetImage = () => {
    console.log("Resetting image....")
    changeImage("unknown")
    onLoadImageVariant({})
    setStyles({ ...defaultStyle })
  }

  const updateImage = (key, additionalStyles = {}) => {
    const toggle = !styles[key]
    changeImage(key)
    onLoadImageVariant(
      clean({
        probability: toggle
          ? key in image.variants
            ? image.variants[key].probability
            : null
          : null,
        label: variantLabel(key),
      })
    )
    setStyles({ ...defaultStyle, [key]: toggle, ...additionalStyles })
  }
  const addNoise = (noiseLevel) =>
    updateImage(`noise${noiseLevel}`, { noise: noiseLevel })

  const augmentVariants = Object.keys(image.variants || {}).filter(
    (x) => !x.startsWith("noise")
  )
  const optionDecorations = {
    hflip: {
      label: () => "Horizontal Flip",
      icon: () => "\\21C4",
    },
    vflip: {
      label: () => "Vertical Flip",
      icon: () => "\\21C5",
    },
    "rot*": {
      label: (x) => `${x.replace("rot", "Rotate ")}°`,
      icon: () => "\\21BB",
    },
  }

  const noop = () => undefined
  const decorate = (object, property, decoration, key) =>
    (object[property] = (decoration[property] || noop)(key))

  const decorateOption = (key) => {
    const defaults = {
      value: key,
      label: key,
      fn: updateImage.bind(null, key),
    }
    let decoration = undefined
    if (key in optionDecorations) {
      decoration = optionDecorations[key]
    } else {
      const decorationKey = Object.keys(optionDecorations)
        .filter((x) => x.includes("*"))
        .find((x) => key.startsWith(x.replace("*", "")))
      if (decorationKey) {
        decoration = optionDecorations[decorationKey]
      }
    }

    if (decoration) {
      decorate(defaults, "label", decoration, key)
      decorate(defaults, "icon", decoration, key)
    }

    return defaults
  }

  const options = [
    {
      value: "normal",
      label: "Normal",
      fn: resetImage,
    },
    ...augmentVariants.map(decorateOption),
  ]

  const noiseLevelVariants = Object.keys(image.variants || {})
    .filter((x) => x.startsWith("noise"))
    .map((x) => parseInt(x.replace("noise", "")))
  const noiseLevels = [0, ...noiseLevelVariants]

  return (
    <TuningsOuterContainer>
      <Centered>
        <TuningSideTile>
          <ControlLabel>Apply Filter:</ControlLabel>
          <TuningSelectContainer>
            <TuningSelect
              options={options}
              value={selectedVariant || options[0]}
              onChange={(x) => {
                setSelectedVariant(x)
                x.fn()
              }}
            />
          </TuningSelectContainer>
          <RangeSlider
            label="Add Noise:"
            step={1}
            min={0}
            max={Math.max(...noiseLevels)}
            value={styles.noise}
            onChange={(level) => {
              setSelectedVariant(null)
              addNoise(level)
            }}
          />
        </TuningSideTile>
      </Centered>
    </TuningsOuterContainer>
  )
}

