const ImageControls = ({ getImageDiv, images, index, onChange }) => {
  const [gameSettings, setGameSettings] = useContext(GameContext)
  const fallbackProbability = 0.5
  const [displayMode, setDisplayMode] = useState(null)
  const [loadedImage, setLoadedImage] = useState({})
  const image = images[index]
  const defaultLoadedImageState = useMemo(
    () =>
      image
        ? {
            probability: image.probability || fallbackProbability,
            label: image.label,
          }
        : { probability: fallbackProbability, label: "unknown" },
    [image]
  )
  useEffect(() => {
    setLoadedImage(defaultLoadedImageState)
  }, [index, defaultLoadedImageState])

  if (!images) return null

  const resetImage = () => {
    styleDiv(getImageDiv, {
      "background-image": `url("img/${image.path}")`,
    })
    setLoadedImage(defaultLoadedImageState)
  }
  const onClickUpdateProperty = (property, guessed) => {
    const updated = { ...gameSettings }
    updated.practiceRound[images[index].path][property] = guessed
    setGameSettings(updated)
  }
  const onAgreementClick = onClickUpdateProperty.bind(null, "agree")
  const onSeenBeforeClick = onClickUpdateProperty.bind(null, "seenbefore")

  const containerSidePadding = 10
  const question = gameSettings.practiceRound[images[index].path]
  return (
    <div style={{ position: "relative" }}>
      {displayMode !== "trick" ? (
        <Caption>
          {AI_NAME} thinks this is a{" "}
          <PictureLabel
            changed={
              isDef(loadedImage.previousLabel) &&
              loadedImage.label !== loadedImage.previousLabel
            }
            title={`Previous: ${loadedImage.previousLabel || "none"}`}
          >
            {loadedImage.label}
          </PictureLabel>
        </Caption>
      ) : null}
      {images[index].variants ? (
        <SwitchMode
          mode={displayMode}
          onSwitchMode={(mode) => {
            resetImage()
            setDisplayMode(mode)
            onChange(clean({ displayMode: mode }))
          }}
        />
      ) : null}
      {displayMode && displayMode === "trick" ? (
        <Tunings
          image={images[index]}
          getImageDiv={getImageDiv}
          onLoadImageVariant={(x) => {
            setLoadedImage({
              ...defaultLoadedImageState,
              ...x,
              previousLabel: loadedImage.label,
            })
            onChange({ ...defaultLoadedImageState, ...x, displayMode })
          }}
          probability={loadedImage.probability}
        />
      ) : null}
      {displayMode && displayMode === "trick" ? (
        <Probability
          value={loadedImage.probability}
          previousLabel={loadedImage.previousLabel}
          label={loadedImage.label}
        />
      ) : null}
      <ImageSliderOverrideStyles images={images} currentIndex={index} />
      {displayMode !== "trick" ? (
        <PracticeVoteButtonsContainer
          containerSize={PRACTICE_IMAGE_SIZE.container}
          containerSidePadding={containerSidePadding}
        >
          <QuestionWithButtons
            question="Do you agree with COMPY?"
            onAnswer={onAgreementClick.bind(null)}
            defaultValue={question.agree}
            index={index}
          />
          <QuestionWithButtons
            question={`Has ${AI_NAME} seen this before?`}
            onAnswer={onSeenBeforeClick.bind(null)}
            defaultValue={question.seenbefore}
            index={index}
          />
        </PracticeVoteButtonsContainer>
      ) : null}
    </div>
  )
}

const PracticeContainer = styled.div`
  display: flex;
  flex-direction: row;
`
const Practice = () => {
  const [currentIndex, setCurrentIndex] = useState(0)
  const [gameSettings, setGameSettings] = useContext(GameContext)
  const [imageState, setImageState] = useState({})
  const reset = () => resetGame(gameSettings, setGameSettings, setCurrentIndex)
  const sliderRef = useRef()
  // eslint-disable-next-line react-hooks/exhaustive-deps
  useEffect(reset, [])

  const currentImage = () =>
    sliderRef.current.children[1].children[0].children[0].children[0]

  if (
    !gameSettings.currentGameData ||
    !gameSettings.currentGameData.scrollingImages
  )
    return null

  return gameSettings.currentGameData ? (
    <Centered>
      <PracticeContainer id="practice-container">
        <div ref={sliderRef}>
          <ImageControls
            getImageDiv={currentImage}
            images={gameSettings.currentGameData.scrollingImages}
            index={currentIndex}
            onChange={setImageState}
          />
          <SimpleImageSlider
            key={gameSettings.version}
            width={PRACTICE_IMAGE_SIZE.container.width}
            height={PRACTICE_IMAGE_SIZE.container.height}
            images={gameSettings.currentGameData.scrollingImages}
            showBullets={imageState.displayMode !== "trick"}
            showNavs={imageState.displayMode !== "trick"}
            navStyle={2}
            onStartSlide={(idx) => {
              setCurrentIndex(idx - 1)
            }}
            style={{
              borderBottomLeftRadius: 15,
              borderTopLeftRadius: 15,
              backgroundColor: null,
              border: "1px solid slategray",
            }}
          />
        </div>
        <PracticeResults resetGame={reset} currentImageIndex={currentIndex} />
      </PracticeContainer>
    </Centered>
  ) : null
}

export default Practice
