import PropTypes from "prop-types"
import styled from "styled-components"
import "./Game.css"
import { isDefined } from "../../util/data"
import { Range } from "react-range"

const CenteredOuter = styled.div`
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  height: 100%;
`
const CenteredInner = styled.div`
  display: flex;
  justify-content: space-around;
  flex-direction: row;
  width: 100%;
`

export const Centered = ({ children }) => (
  <CenteredOuter>
    <CenteredInner>{children}</CenteredInner>
  </CenteredOuter>
)

const ToggleButtonCheckBoxWrapper = styled.div`
  position: relative;
  background-color: lightgray;
  padding: 5px;
  border-radius: 4px;
`

const CheckButton = styled.button`
  background: ${({ checked, color }) => (checked ? color : "gray")};
  padding: 5px;
  color: ${({ checked }) => (checked ? "white" : "black")};
  outline: none;
  border: none;
  border-radius: ${({ left }) => (left ? "4px 0 0 4px" : "0 4px 4px 0")};
  transition: text-shadow 0.25s ease;
  &:hover {
    cursor: pointer;
    text-shadow: ${({ checked }) =>
      checked ? "1px 1px 1px black;" : "1px 1px 1px lightgray;"};
  }
`

export const ToggleButton = ({
  trueLabel = "YES",
  falseLabel = "NO",
  trueBgColor,
  falseBgColor,
  onToggle,
  defaultValue,
}) => {
  return (
    <ToggleButtonCheckBoxWrapper>
      <CheckButton
        checked={isDefined(defaultValue) && defaultValue}
        color={trueBgColor}
        onClick={() => onToggle(true)}
        left={true}
      >
        {trueLabel}
      </CheckButton>
      <CheckButton
        checked={isDefined(defaultValue) && !defaultValue}
        color={falseBgColor}
        onClick={() => onToggle(false)}
        right={true}
      >
        {falseLabel}
      </CheckButton>
    </ToggleButtonCheckBoxWrapper>
  )
}

ToggleButton.propTypes = {
  onToggle: PropTypes.func.isRequired,
  trueLabel: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
  falseLabel: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
}

export const GameButton = styled.button`
  border: 1px solid lightgray;
  text-transform: uppercase;
  font-family: Futura, sans-serif;
  font-size: 1.4rem;
  padding: 5px;
  border-bottom: 5px solid grey;
  color: darkslategray;
  margin-top: 10px;
  cursor: pointer;
  &:disabled {
    cursor: auto;
    color: lightgray;
  }
`

export const Emoji = ({ className, style, label, symbol }) => (
  <span className={className} style={style} role="img" aria-label={label}>
    {String.fromCodePoint(symbol)}
  </span>
)

export const Sidecar = styled.div`
  background-color: white;
`

const RangeContainer = styled.div`
  padding: 5px;
  margin: 5px;
`

export const ControlLabel = styled.div`
  margin-bottom: 10px;
  font-size: 0.9rem;
  text-align: center;
`

const RangeTrack = styled.div`
  height: 6px;
  width: 100%;
  background-color: #ccc;
`

const RangeHandle = styled.div`
  height: 18px;
  width: 18px;
  background-color: #999;
  border: 1px solid #555;
  font-size: 0.7rem;
  text-align: center;
  line-height: 1.7;
  outline: none;
  border-radius: 4px;
`

export const RangeSlider = ({
  value,
  min,
  max,
  onChange,
  step = 1,
  label = null,
}) => {
  return (
    <RangeContainer>
      {label ? <ControlLabel>{label}</ControlLabel> : null}
      <Range
        step={step}
        min={min}
        max={max}
        values={[value]}
        onChange={(values) => {
          const [_value] = values
          if (_value !== value) {
            onChange(_value)
          }
        }}
        renderTrack={({ props, children }) => (
          <RangeTrack {...props}>{children}</RangeTrack>
        )}
        renderThumb={({ props }) => (
          <RangeHandle {...props}>{value}</RangeHandle>
        )}
      />
    </RangeContainer>
  )
}
