import styled from "styled-components"

const Container = styled.div`
  margin-top: 2px;
  background-color: #444;
  color: white;
  display: flex;
  flex-direction: column;
  font-family: Futura;
  min-width: 400px;
  max-width: 400px;
  width: 400px;
`

const Board = styled.div`
  margin: 6px;
  border: 5px solid white;
  border-radius: 10px;
  flex-direction: row;
`

const SideContainer = styled.div`
  display: flex;
  flex-direction: row;
`

const Side = styled.div`
  flex: 1;
  text-align: center;
`
const Team = styled.div`
  padding: 10px;
  text-align: center;
  font-size: 24pt;
  border-bottom: 5px solid white;
  margin: 0;
  font-weight: bold;
`
const ScoreContainer = styled.div`
  padding: 10px;
`
const Score = styled.div`
  font-size: 48pt;
  font-family: Impact, sans-serif;
`
const Bottom = styled.div`
  font-size: 18;
  padding: 5px;
  border-top: 5px solid white;
  font-weight: bold;
  text-align: center;
`

const ScoreBoard = ({
  home,
  away,
  bottomDisplay = () => "",
  totalPossible = 0,
}) => {
  const ScoreboardSide = ({ player, style = {} }) => {
    const scoreDisplay =
      totalPossible > 0 ? `${player.score} / ${totalPossible}` : player.score
    const percentageDisplay =
      totalPossible > 0 ? (
        <div>({((player.score / totalPossible) * 100).toFixed()}%)</div>
      ) : null
    return (
      <Side style={style}>
        <Team style={{ color: player.color }}>{player.name}</Team>
        <ScoreContainer>
          <Score>{scoreDisplay}</Score>
          {percentageDisplay}
        </ScoreContainer>
      </Side>
    )
  }

  const bottom = bottomDisplay()

  return (
    <Container>
      <Board>
        <SideContainer>
          <ScoreboardSide player={home} />
          <ScoreboardSide
            player={away}
            style={{ borderLeft: "3px solid white" }}
          />
        </SideContainer>
        {bottom ? <Bottom>{bottom}</Bottom> : null}
      </Board>
    </Container>
  )
}

export default ScoreBoard
