import { GameContext } from "../../context/GameContext"
import { useContext, useEffect } from "react"
import styled from "styled-components"
import Gallery from "react-grid-gallery"
import CheckButton from "react-grid-gallery/lib/CheckButton"
import ScoreBoard from "./Scoreboard"
import { Centered, GameButton, Sidecar } from "./Util"
import { gameData, isDefined, GAME_IMAGE_COUNT } from "../../util/data"
import "./Game.css"

const SIZES = {
  thumbnail: {
    height: 32,
    width: 32,
  },
  grid: {
    width: 710,
  },
}

const generateGameData = async () => {
  const _gameData = await gameData()
  const images = _gameData.gameOptions.reduce(
    (all, x, i) => ({ ...all, [i]: x }),
    {}
  )
  return {
    gameData: _gameData,
    images,
    correctAnswers: Object.keys(images)
      .filter((i) => images[i].correct)
      .map((i) => parseInt(i)),
  }
}

const labelStyle = {
  padding: "5px",
  fontSize: "75%",
  color: "white",
  background: "rgba(0,0,0,0.5)",
}

const toImage = (image, index, tags = () => []) => {
  const img = {
    src: `img/${image.path}`,
    thumbnail: `img/${image.path}`,
    thumbnailWidth: SIZES.thumbnail.width,
    thumbnailHeight: SIZES.thumbnail.height,
    index,
    correct: image.correct,
    customOverlay: <SelectionsOverlay index={index} />,
  }
  img.tags = tags(img) || []
  return img
}
const toDeerImage = (img, index) =>
  toImage(img, index, (_img) => [
    {
      value: `${img.label}`,
      title: `COMPY Says: ${img.label}`,
    },
  ])

const findYouPlayer = (gameSettings) =>
  gameSettings && gameSettings.players && gameSettings.players.length
    ? gameSettings.players[0]
    : null

const findCarnacPlayer = (gameSettings) =>
  gameSettings && gameSettings.players && gameSettings.players.length
    ? gameSettings.players[1]
    : null

const SelectionsOverlayContainer = styled.div`
  color: red;
  font-weight: bold;
  text-align: right;
  position: absolute;
  top: 0;
  right: 0;
  background-color: ${({ correct }) =>
    correct ? "rgba(75, 75, 75, 0.8)" : "rgba(75, 75, 75, 0.0)"};
  width: 100%;
  height: 100%;
`

const CheckButtonContainer = styled.div`
  position: absolute;
  top: 0;
  right: 0%;
`

const SelectionsOverlay = ({ index }) => {
  const [gameSettings] = useContext(GameContext)
  const _carnac = findCarnacPlayer(gameSettings)
  const _you = findYouPlayer(gameSettings)
  return (
    <SelectionsOverlayContainer
      correct={isDefined(_you.score) && !gameSettings.images[index].correct}
    >
      {_carnac.score && _carnac.selections.includes(parseInt(index)) ? (
        <CheckButtonContainer>
          <CheckButton color={_carnac.color} isSelected={true} />
        </CheckButtonContainer>
      ) : null}
    </SelectionsOverlayContainer>
  )
}

const Choices = () => {
  const [gameSettings, setGameSettings] = useContext(GameContext)

  useEffect(() => {
    resetGame(gameSettings, setGameSettings) // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  const onSelectImage = (index) => {
    const updated = { ...gameSettings }
    const you = findYouPlayer(updated)
    if (you.score) return
    var img = updated.images[index]
    if (img.hasOwnProperty("isSelected")) img.isSelected = !img.isSelected
    else img.isSelected = true
    you.selections = updated.images
      .map((x, index) => ({ ...x, index }))
      .filter((x) => x.isSelected)
      .map((x) => x.index)
    setGameSettings(updated)
  }

  const you = findYouPlayer(gameSettings)
  return (
    <div>
      <Gallery
        images={gameSettings.images}
        enableImageSelection={!you.score}
        onSelectImage={onSelectImage}
        onClickThumbnail={onSelectImage}
        tagStyle={labelStyle}
        showLightboxThumbnails={true}
        enableLightbox={false}
      />
    </div>
  )
}

const resetGame = async (original, updateFn) => {
  const updated = { ...original }
  const currentGameData = await generateGameData()
  updated.currentGameData = currentGameData
  updated.images = Object.keys(currentGameData.images).map((i) =>
    toDeerImage(currentGameData.images[i], i)
  )
  const _carnac = findCarnacPlayer(updated)
  const _you = findYouPlayer(updated)
  _carnac.score = null
  _you.score = null

  const carnacAnswers = () => {
    return Object.keys(currentGameData.images)
      .filter((i) => {
        const img = currentGameData.images[i]
        return (
          ("carnac" in img && img.carnac) ||
          (!("carnac" in img) && img.label === "deer")
        )
      })
      .map((i) => parseInt(i))
  }
  _carnac.selections = carnacAnswers()
  _you.selections = []
  updateFn(updated)
}

const calculateScore = (gameData, selections) => {
  const _selections = selections || []
  const wrongAnswers = _selections.filter(
    (x) => !gameData.correctAnswers.includes(x)
  )
  const omittedAnswers = gameData.correctAnswers.filter(
    (x) => !_selections.includes(x)
  )

  return (
    Object.keys(gameData.images).length -
    wrongAnswers.length -
    omittedAnswers.length
  )
}

const ChallengeCarnacContainer = styled.div`
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
`

const ChallengeCarnac = ({ onClick }) => {
  const [gameSettings] = useContext(GameContext)
  const selectionCount = gameSettings.players[0].selections.length
  return (
    <ChallengeCarnacContainer>
      <div>
        <p>
          COMPY has seen a lot of deer images before, and he is really good at
          identifying a deer when he sees one.
        </p>
        <p>
          Can you tell which of the following deer images COMPY may have seen?
          There is at least one.
        </p>
        <p>Select them and then hit the "Challenge Carnac" button (below).</p>
        <p>
          You will be playing against Carnac and whomever gets more correct,
          wins!
        </p>
        <p style={{ textAlign: "center", fontWeight: "bold" }}>
          {selectionCount > 0
            ? `${selectionCount} picture(s) selected`
            : "1 or more selections required to play"}
        </p>
      </div>
      <div style={{ display: "flex", flexDirection: "column" }}>
        <GameButton onClick={onClick} disabled={selectionCount === 0}>
          Challenge Carnac!
        </GameButton>
      </div>
    </ChallengeCarnacContainer>
  )
}

const InformationContainer = styled(Sidecar)`
  display: flex;
  margin-top: 2px;
  height: 99%;
  border: 1px solid darkgray;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
`

const Information = () => {
  const [gameSettings, setGameSettings] = useContext(GameContext)
  const carnac = findCarnacPlayer(gameSettings)

  const loadScores = () => {
    const updated = { ...gameSettings }
    const _carnac = findCarnacPlayer(updated)
    const _you = findYouPlayer(updated)
    _carnac.score = calculateScore(
      gameSettings.currentGameData,
      _carnac.selections
    )
    _you.score = calculateScore(gameSettings.currentGameData, _you.selections)
    setGameSettings(updated)
  }
  return (
    <InformationContainer>
      {carnac.score ? <Results /> : <ChallengeCarnac onClick={loadScores} />}
    </InformationContainer>
  )
}

const ResultsContainer = styled.div`
  width: 100%;
  margin: 10px;
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
`

const Results = () => {
  const [gameSettings, setGameSettings] = useContext(GameContext)
  const carnac = findCarnacPlayer(gameSettings)
  const you = findYouPlayer(gameSettings)
  const playAgain = () => resetGame(gameSettings, setGameSettings)
  const winner = () =>
    you.score > carnac.score ? you : you.score < carnac.score ? carnac : null
  const winnerMessage = () => {
    const _winner = winner()
    return _winner
      ? `The winner is ${_winner.name}!`
      : `${you.name} tied ${carnac.name}!`
  }
  return (
    <ResultsContainer>
      <div style={{ marginTop: 15 }}>
        <ScoreBoard
          home={you}
          away={carnac}
          bottomDisplay={winnerMessage}
          totalPossible={GAME_IMAGE_COUNT}
        />
      </div>
      <div style={{ display: "flex", flexDirection: "column", width: "100%" }}>
        <GameButton onClick={playAgain}>Play Again!</GameButton>
      </div>
    </ResultsContainer>
  )
}

const GameContainer = styled.div`
  display: flex;
  flex-direction: row;
`

const FullGame = () => {
  return (
    <Centered>
      <GameContainer>
        <div style={{ minWidth: SIZES.grid.width, maxWidth: SIZES.grid.width }}>
          <Choices />
        </div>
        <div style={{ maxWidth: 440, minWidth: 440 }}>
          <Information />
        </div>
      </GameContainer>
    </Centered>
  )
}

export default FullGame
